@import './src/style/vatibles';

.form{
  &__wrapper{
    display: flex;
    width: 100%;
    flex-direction: column;
  }

  &__input-wrapper{
    display: flex;
    flex-direction: column;
  }

  &__input-wrap{
    color: $text-color;
    font-weight: 600;
    font-size: $font-size-text;
    background: $white-color;
    box-sizing: border-box;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &--container{
      border: 1px solid $border-blue;
    }

    &--erorr {
      border: 1px solid #FF4B26;
    }

    &--complete{
      border: 1px solid #6FCF97;
    }

  }





  &__input{

    width: 200px;
    border-radius: 8px;
    border: 0;
    padding: 18px 20px;
    color: #333333;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    background: #FFFFFF;
    box-sizing: border-box;

    &:focus {
      outline: none;
    }
  }

  &__link-forgot-pass{
    text-decoration: none;
    width: 100%;
    margin-right: 20px;
    font-weight: 600;
    font-size: 14px;
    text-align: center;
    color: #75777A;
  }



  &__input-header{
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__input-name {
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    color: #333333;
  }

  &__err-message{
    font-weight: normal;
    font-size: 12px;
    color: #FF4B26;
  }

  &__submit-button{
    margin-top: 30px;
    background: #065BEA;
    border-radius: 8px;
    height: 54px;
    width: 100%;
    border: none;

    font-weight: 600;
    font-size: 16px;
    color: #FFFFFF;

    &:hover{
      background: #0451D2;
    }

    &:disabled{
      background: #C6D0E0;
    }



  }
}